html {
  box-sizing: border-box;
  font-size: 62.3%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  background-color: #F5F5FA;
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1000px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 4rem;
}

.header .header__title {
  font-size: 3.2rem;
}
.header .header__sub {
  font-size: 1.6rem;
  color: #848199;
}

.cardContainer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 5rem;
  row-gap: 5rem;
}
.cardContainer .card {
  position: relative;
  width: 25rem;
  height: 35rem;
  padding: 4rem 3rem;
  border-radius: 2rem;
  background-color: white;
  margin-right: -1.2rem;
  margin-left: -1.2rem;
  transition: all 0.3s ease;
}
.cardContainer .card .card__price_number {
  font-size: 2rem;
  color: #373737;
}
.cardContainer .card .card__price_time {
  font-size: 1.2rem;
  color: #848199;
}
.cardContainer .card .card__header {
  margin-top: 1rem;
}
.cardContainer .card .card__header span {
  font-size: 2.2rem;
  color: #373737;
}
.cardContainer .card .card__description p {
  font-size: 1.1rem;
  color: #848199;
}
.cardContainer .card .benefits {
  margin-top: 2rem;
}
.cardContainer .card .benefits .logo {
  height: 2rem;
  margin-right: 0.5rem;
}
.cardContainer .card .benefits ul {
  list-style: none;
  padding-left: 0;
  font-size: 1rem;
}
.cardContainer .card .benefits li {
  display: flex;
  margin-top: 1rem;
  align-items: center;
  color: #848199;
}
.cardContainer .card .card__button {
  font-size: 1.2rem;
  width: 100%;
  padding: 1rem;
  border-radius: 2rem;
  margin-top: 2rem;
  color: #FF8E3C;
  border: none;
  background-color: #fff4eb;
  transition: all 0.3s ease;
}
.cardContainer .card .card__button:hover {
  cursor: pointer;
  background: #373737;
  color: white;
}
.cardContainer .card.selec {
  transform: translateY(-4rem);
  position: relative;
  z-index: 1;
  box-shadow: 2px 20px 30px 1px #848199, 0px 4rem 0px 0px white;
  background: #FF8E3C;
}
.cardContainer .card.selec p, .cardContainer .card.selec span, .cardContainer .card.selec li {
  color: #373737;
}
.cardContainer .card.selec .card__button {
  cursor: pointer;
  background: #373737;
  color: white;
}

.special {
  position: absolute;
  top: 1rem;
  right: 3rem;
  font-size: 1rem;
  background: #fff4eb;
  padding: 1rem 2rem;
  border-radius: 2rem;
  color: #373737;
}